<template>
  <div id="pos">
    <el-row>
      <el-col :span="7" class="pos-order" id="order-list">
        <el-tabs>
          <el-tab-pane label="点餐">
            <el-table :data="tableDate" border style="width:100%">
              <el-table-column prop="goodsName" label="商品名称"></el-table-column>
              <el-table-column prop="count" label="量" width="50"></el-table-column>
              <el-table-column prop="price" label="金额" width="70"></el-table-column>
              <el-table-column label="操作" width="100" fixed="right">
                <template scope="scope">
                  <el-button type="text" size="small" @click="desSingleGoods(scope.row)">删除</el-button>
                  <el-button type="text" size="small" @click="addOrderList(scope.row)">增加</el-button>
                </template>
              </el-table-column>
            </el-table>
            <div class="totalDiv">
              <small>数量：</small>
              {{totalCount}}
              &nbsp;&nbsp;&nbsp;
              <small>金额：</small>
              {{totalMoney}}元
            </div>
            <div class="div-btn">
              <el-button type="warning">挂单</el-button>
              <el-button type="danger" @click="delAllGoods()">删除</el-button>
              <el-button type="success" @click="checkout()">结账</el-button>
            </div>
          </el-tab-pane>
          <el-tab-pane label="挂单">挂单</el-tab-pane>
          <el-tab-pane label="外卖">外卖</el-tab-pane>
        </el-tabs>
      </el-col>

      <el-col :span="17">
        <div class="often-goods">
          <div class="title">常用商品</div>
          <div class="often-goods-list">
            <ul>
              <li v-for="(goods,id) in oftenGoods" :key="id" @click="addOrderList(goods)">
                <span>{{goods.goodsName}}</span>
                <span class="o-price">￥{{goods.price}}元</span>
              </li>
            </ul>
          </div>
        </div>

        <div class="goods-type">
          <el-tabs>
            <el-tab-pane label="汉堡">
              <div>
                <ul class="cookList">
                  <li
                    v-for="(goods,id) in type0Goods"
                    :key="id"
                    class="one-food-info"
                    @click="addOrderList(goods)"
                  >
                    <span class="foodImg">
                      <img :src="goods.goodsImg" width="100%" />
                    </span>
                    <span class="foodName">{{goods.goodsName}}</span>
                    <span class="foodPrice">￥{{goods.price}}.00元</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label="小食">
              <div>
                <ul class="cookList">
                  <li
                    v-for="(goods,id) in type0Goods"
                    :key="id"
                    class="one-food-info"
                    @click="addOrderList(goods)"
                  >
                    <span class="foodImg">
                      <img :src="goods.goodsImg" width="100%" />
                    </span>
                    <span class="foodName">{{goods.goodsName}}</span>
                    <span class="foodPrice">￥{{goods.price}}.00元</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label="饮料">
              <div>
                <ul class="cookList">
                  <li
                    v-for="(goods,id) in type0Goods"
                    :key="id"
                    class="one-food-info"
                    @click="addOrderList(goods)"
                  >
                    <span class="foodImg">
                      <img :src="goods.goodsImg" width="100%" />
                    </span>
                    <span class="foodName">{{goods.goodsName}}</span>
                    <span class="foodPrice">￥{{goods.price}}.00元</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label="套餐">
              <div>
                <ul class="cookList">
                  <li
                    v-for="(goods,id) in type0Goods"
                    :key="id"
                    class="one-food-info"
                    @click="addOrderList(goods)"
                  >
                    <span class="foodImg">
                      <img :src="goods.goodsImg" width="100%" />
                    </span>
                    <span class="foodName">{{goods.goodsName}}</span>
                    <span class="foodPrice">￥{{goods.price}}.00元</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "pos",
  data() {
    return {
      tableDate: [
        // {
        //   goodsName: "可口可乐",
        //   price: 8,
        //   count: 1,
        // },
        // {
        //   goodsName: "香辣鸡腿堡",
        //   price: 15,
        //   count: 1,
        // },
        // {
        //   goodsName: "爱心薯条",
        //   price: 8,
        //   count: 1,
        // },
        // {
        //   goodsName: "甜筒",
        //   price: 8,
        //   count: 1,
        // },
      ],
      oftenGoods: [],
      type0Goods: [],
      type1Goods: [],
      type2Goods: [],
      type3Goods: [],
      totalMoney: 0,
      totalCount: 0,
    };
  },
  created() {
    axios
      .get("http://jsonplaceholder.typicode.com/todos?_limit=6")
      .then((response) => {
        console.log(response);
        // this.oftenGoods = response.data;
        // this.oftenGoods.goodsId=response.data.
        // let arr=["哈哈","嘿嘿","嘻嘻"];
        this.oftenGoods = response.data.map(function (v, i, arr) {
          return {
            goodsId: v.id,
            goodsName: v.title,
            price: v.userId,
            goodsImg: "http://699pic.com/tupian-500639389.html",
          };
        });
        // console.log(this.oftenGoods, "改造数据");
      })
      .catch((error) => {
        console.log(error);
        alert("网络错误404，亲您稍后继续访问！");
      });
    //汉堡
    axios
      .get("http://jsonplaceholder.typicode.com/todos?_limit=4")
      .then((response) => {
        // this.type0Goods = response.data;
        this.type0Goods = response.data.map((v, i, arr) => {
          return {
            goodsId: v.id,
            goodsName: v.title,
            price: v.userId,
            goodsImg:
              "https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=1861781808,1434365906&fm=26&gp=0.jpg",
          };
        });
      })
      .catch((err) => {
        console.log(err);
      });
  },
  mounted: function () {
    var orderHeight = document.body.clientHeight;
    document.getElementById("order-list").style.height = orderHeight + "px";
  },
  methods: {
    addOrderList(goods) {
      console.log(goods, "我弟弟");
      this.totalMoney = 0;
      this.totalCount = 0;

      //先看商品是否已经存在于订单列表中
      let isHave = false;
      for (let i = 0; i < this.tableDate.length; i++) {
        // console.log(this.tableDate, "表格");
        if (this.tableDate[i].goodsId === goods.goodsId) {
          isHave = true;
        }
      }
      //然后再根据判断的值编写业务逻辑
      if (isHave) {
        console.log("此处执行力if语句");
        //改变列表中商品的数量
        let arr = this.tableDate.filter((o) => o.goodsId == goods.goodsId);
        console.log(arr, "shuzu ");
        arr[0].count++;
      } else {
        console.log("此处执行了else语句");
        let newGoods = {
          goodsId: goods.goodsId,
          goodsName: goods.goodsName,
          price: goods.price,
          count: 1,
        };
        // if()
        this.tableDate.push(newGoods);
      }
      //计算汇总的价格和数量
      this.getAllMoney();
    },
    //删除单个商品
    desSingleGoods(goods) {
      this.tableDate = this.tableDate.filter((o) => o.goodsId != goods.goodsId);
      this.getAllMoney();
    },
    //刪除全部商品
    delAllGoods() {
      this.tableDate = [];
      this.totalMoney = 0;
      this.totalCount = 0;
    },
    //结账
    checkout() {
      if (this.totalCount != 0) {
        this.tableData = [];
        this.totalCount = 0;
        this.totalMoney = 0;
        this.$message({
          message: "结账成功，感谢你又为店里出了一份力!",
          type: "success",
        });
      } else {
        this.$message.error("不能空结。老板了解你急切的心情！");
      }
    },
    //汇总数量金额
    getAllMoney() {
      this.totalCount = 0;
      this.totalMoney = 0;
      if (this.tableDate) {
        //计算汇总金额和数量
        this.tableDate.forEach((element) => {
          console.log(this.tableDate, "我的数据");
          this.totalCount += element.count;
          this.totalMoney = this.totalMoney + element.price * element.count;
        });
      }
    },
  },
};
</script>
<style scoped>
.pos-order {
  background-color: #f9fafc;
  border-right: 1px solid C0CCDA;
}
.div-btn {
  margin-top: 10px;
}
.title {
  height: 18px;
  border-bottom: 1px solid #d3dce6;
  background-color: #f9fafc;
  padding: 10px;
  text-align: left;
}
.often-goods-list ul li {
  list-style: none;
  float: left;
  border: 1px solid #e5e9f2;
  padding: 10px;
  margin: 10px;
  background-color: #fff;
  cursor: pointer;
}
.o-price {
  color: #58b7ff;
}
.goods-type {
  clear: both;
}
.cookList li {
  list-style: none;
  width: 23%;
  border: 1px solid #e5e9f2;
  height: auot;
  overflow: hidden;
  background-color: #fff;
  padding: 2px;
  float: left;
  margin: 2px;
  cursor: pointer;
}
.cookList li span {
  display: block;
  float: left;
}
.foodImg {
  width: 40%;
}
.foodName {
  font-size: 15px;
  padding-left: 10px;
  color: brown;
}
.foodPrice {
  font-size: 16px;
  padding-left: 10px;
  padding-top: 10px;
}
.one-food-info {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 116px;
  overflow: hidden;
}
.totalDiv {
  background-color: #fff;
  padding: 10px;
  border-bottom: 1px solid #d3dce6;
}
</style>